import axios from 'axios'
import moment from 'moment';

const filesMD = require.context('/public/reso', true, /\.md$/);
const filesMDNames = filesMD.keys();
const tmepListDatas = [];
const tagAll = new Set();
const fileSystem = createNode('reso',true,[],{});

var id = 1;
init();
 function init(){
   filesMDNames.map((item) => {
    const listObj = {};
    listObj.id = id++;
    listObj.comment = 0;
    listObj.watch = 0;
    const listItemS = item.split('/');
    if (listItemS.length > 0) {
      let listItemCopy = [...listItemS];
      listItemCopy[listItemCopy.length-1] = listItemCopy[listItemCopy.length-1].replace('.md', '');
      listItemCopy.shift();
      listObj.title = listItemCopy.join('-');
      listObj.path = item.replace('.\/','@\/..\/reso\/');
    }
    const imgSrc =
      "https://cdn2.hubspot.net/hubfs/322787/Mychefcom/images/BLOG/Header-Blog/photo-culinaire-pexels.jpg"
      listObj.imgSrc = imgSrc;
      ExtractProperties(listObj);
      
    return tmepListDatas.push(listObj);
  });
  
  ExtractFileSystem(tmepListDatas)
  console.log(fileSystem);
}

function ExtractFileSystem(tmepListDatas){
  tmepListDatas.map((item) => {
    let {id, path} = item;
    path = path.replace('@/../reso/', '')
    let patharr = path.split('/');
    buildFileSystemTree(patharr,fileSystem,item)
  })
}

function createNode(label, isFolder, children = [], info = {}){
  let newNode = {};
  newNode.label = label;
  newNode.isFolder = isFolder;
  newNode.children = children;
  newNode.info = info;
  return newNode;
}

// arr是当前还没有安进去的路径数组，node是对象点，info是要填的节点信息
function buildFileSystemTree(arr, node, info){
  if(arr.length === 1) {
    let newnode = createNode(arr[0].replace('.md', ''), false, [], info);
    node.children.push(newnode);
    return;
  }
  else {
    let nextnode = null;
    for(let it of node.children){
      if(it.label === arr[0]){
        nextnode = it;break;
      }
    }
    if(nextnode){
      arr.shift()
      buildFileSystemTree([...arr], nextnode, info);
    }
    else {
      nextnode = createNode(arr[0], true, [], {});
      arr.shift();
      node.children.push(nextnode);
      buildFileSystemTree([...arr], nextnode, info);
    }
  }
}

function ExtractProperties(obj){
  let source = [];
  axios.get(obj.path).then(res => {
    source = res.data.split("\n");
    let headInfo = [];
    let flag = 0;
    let i = 0;
    for (i = 0; i < source.length; i++) {
      if (source[i].trim() === "---" && flag === 1) {
        break;
      } else if (source[i].trim() === "---" && flag === 0) {
        flag++;
      } else if (flag === 1) {
        headInfo.push(source[i].trim())
      }
    }
    source.splice(0, i+1);
    source = source.join("\n")
  
    parseInfo(headInfo, source, obj);
    for(let item of obj.tags) {
      tagAll.add(item);
    }
    tmepListDatas.sort((a,b) => {
      return moment(b.time).unix() - moment(a.time).unix();
    })

  })
}

function parseInfo(info, content, obj){
  for(let i = 0; i < info.length; i++){
    if(info[i].match('title')){
      obj.title = info[i].split(':')[1];
    }
    else if(info[i].match('time')){
      let newArr = info[i].split(':');
      newArr.shift();
      obj.time = newArr.join(':');
    }
    else if(info[i].match('categories')){
      obj.categories = info[i].split(':')[1].split(',');
    }
    else if(info[i].match('tags')){
      obj.tags = info[i].split(':')[1].split(',');
    }
    else if(info[i].match('summary')){
      obj.summary = info[i].split(':')[1];
    }
  }
  obj.content = content;
  return obj;
}

function extractDataByArr(arr) {
  return tmepListDatas.filter(item => arr.includes(item.id) )
}

export {tmepListDatas, extractDataByArr, tagAll, fileSystem};